html {
    width: 100%;
    height: 100%;
    margin: 0;

    background: #AA076B;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #61045F, #AA076B);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #61045F, #AA076B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}


.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    // height: 100vh;
    // margin: 0 1vmin;

    color: white;
    text-align: center;
    /* background-color: #282c34; */
    // background: #AA076B;  /* fallback for old browsers */
    // background: -webkit-linear-gradient(to top, #61045F, #AA076B);  /* Chrome 10-25, Safari 5.1-6 */
    // background: linear-gradient(to top, #61045F, #AA076B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.logo {
    max-width: 250px;
    height: auto;
    margin: 3vmin 0 0 0;
}

.header-section {
    margin: 0;
    h2 {

        // font-family: 'Pacifico', cursive;
        font-family: 'Warnes', cursive;
        // font-family: 'Audiowide', cursive;
        font-size: calc(24px + (36 - 24) * ((100vw - 300px) / (1800 - 300)));
        line-height: calc(28px + (48 - 28) * ((100vw - 300px) / (1800 - 300)));
        letter-spacing: 2px;
        // color: #1673C4;
        color: #fff;
        //  -webkit-text-stroke: 0.5px #fff;
        //  -webkit-text-fill-color: #fff;
        text-decoration: none;

          animation: glow 1s ease-in-out infinite alternate;


    // text-shadow:
    //     3px 3px 0 #fff,
    //     -1px -1px 0 #fff,  
    //     1px -1px 0 #fff,
    //     -1px 1px 0 #fff,
    //     1px 1px 0 #fff;

        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        transition: all 1.0s;
        
        &:hover {
            color: #0DB1DB;
        //     -webkit-text-stroke: 1px #0DB1DB;
        //  -webkit-text-fill-color: #fff;
            -webkit-animation: neon3 1.0s ease-in-out;
            -moz-animation: neon3 1.0s ease-in-out  alternate;
            animation: neon3 1.0s ease-in-out;
        }

    }
}

        @keyframes glow {
        from {
            text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #0073e6, 0 0 20px #0073e6, 0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6;
        }
        to {
            text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #0073e6, 0 0 40px #0073e6, 0 0 50px #0073e6, 0 0 60px #0073e6, 0 0 70px #0073e6;
        }
        }

        @-webkit-keyframes neon3 {
            from {
                text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #FFDD1B, 0 0 70px #FFDD1B, 0 0 80px #FFDD1B, 0 0 100px #FFDD1B, 0 0 150px #FFDD1B;
            }
            to {
                text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FFDD1B, 0 0 35px #FFDD1B, 0 0 40px #FFDD1B, 0 0 50px #FFDD1B, 0 0 75px #FFDD1B;
            }
        }

        @-moz-keyframes neon3 {
            from {
                text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #FFDD1B, 0 0 70px #FFDD1B, 0 0 80px #FFDD1B, 0 0 100px #FFDD1B, 0 0 150px #FFDD1B;
            }
            to {
                text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FFDD1B, 0 0 35px #FFDD1B, 0 0 40px #FFDD1B, 0 0 50px #FFDD1B, 0 0 75px #FFDD1B;
            }
        }

        @keyframes neon3 {
            from {
                text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #FFDD1B, 0 0 70px #FFDD1B, 0 0 80px #FFDD1B, 0 0 100px #FFDD1B, 0 0 150px #FFDD1B;
            }
            to {
                text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FFDD1B, 0 0 35px #FFDD1B, 0 0 40px #FFDD1B, 0 0 50px #FFDD1B, 0 0 75px #FFDD1B;
            }
        }


h2.progress-header {
    margin: 1vmin 0 2vmin 0;
    font-family: 'Lora', serif;
    font-size: calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));
    font-weight: 800;

    color: #ffc107;
}


h1 {
    margin: 2vmin 0;
}


.question-text {
    /* font-family: 'Quicksand', sans-serif; */
    margin: 0 4vmin 2vmin 4vmin;
    // font-family: 'Lora', serif;
    font-family: 'Roboto', sans-serif;
    font-size: calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));
    line-height: calc(28px + (36 - 28) * ((100vw - 300px) / (1800 - 300)));
    font-weight: 800;
}



.answer {
    width: 900px;
    max-width: 90vw;
    margin-top: 10px;
    padding: 1vmin;

    font-family: 'Lora', serif;
    font-size: calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));
    line-height: calc(22px + (28 - 22) * ((100vw - 300px) / (1800 - 300)));
    text-align: left;
    background-color: #fff;

    border: 1px solid #fff;
    border-radius: 10px;
    cursor: pointer;
    transition: ease-in-out, width 0.35s ease-in-out;



    &:hover {
        color: #fff;
        background-color: #0DD1C4;
    }

    &:focus {
        color: #fff;
        outline: none;
    }

}
.selected {
    font-weight: 700;

    -webkit-text-fill-color: #fff;
    background-color: #18A2AB;
}

.letter {
    /* position: absolute; */
    width: 30px;
    height: 28px;
    top: 0;
    padding: 5px;
    left: 0;

    font-family: 'Lora', serif;
    text-align: center;
    color: #4f4c4c;
}

/* Results */

.results {
    margin: 3vmin 0 0 0;
    
    h2 {
        margin: 1vmin 0 2vmin 0;
        font-family: 'Roboto', sans-serif;
        font-size: calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));
        font-weight: 800;

        color: #ffc107;
    }

    ul {
        margin: 0 4vmin;
        font-size: calc(18px + (26 - 18) * ((100vw - 300px) / (1800 - 300)));
        line-height: calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));;
    }

    span.correct {
        color: #0DB1DB;
        font-weight: 800;
    }
    span.failed {
        color: #f27c7c;
        font-weight: 800;
    }

}





.error {
    color: #f27c7c;
    font-weight: 800;
}



/* Button */

.btn {
    margin-top: 20px;
    padding: 10px 50px;
    text-transform: uppercase;
    font-size: 18px;
    cursor: pointer;
    color: #4f4c4c;
    background-color: #f4f4f4;
    transition: 0.4s;
    border: 2px solid #fff;
    border-radius: 5px;
}

.btn:hover {
    color: #fff;
    background-color: #ffce3c;
    border: 2px solid #fff;
    transform: scale(1.1);

}

.btn-primary {
    margin: 2vmin 0;
    color: #4f4c4c;
    background-color: #ffc107;
    font-weight: bold;
}


// General

#no-wrap {
    white-space: nowrap;
}