@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cardo:wght@400;700&family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css?family=Warnes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent}img{object-fit:contain}fieldset,form,h3{-webkit-margin-start:0 !important;margin-inline-start:0 !important;-webkit-margin-end:0 !important;margin-inline-end:0 !important;-webkit-padding-start:0 !important;padding-inline-start:0 !important;-webkit-padding-end:0 !important;padding-inline-end:0 !important;-webkit-padding-before:0 !important;padding-block-start:0 !important;-webkit-padding-after:0 !important;padding-block-end:0 !important}body{line-height:1}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}nav ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}a{margin:0;padding:0;font-size:100%;vertical-align:baseline;background:transparent}ins{background-color:#ff9;color:#000;text-decoration:none}mark{background-color:#ff9;color:#000;font-style:italic;font-weight:bold}del{text-decoration:line-through}abbr[title],dfn[title]{border-bottom:1px dotted;cursor:help}table{border-collapse:collapse;border-spacing:0}hr{display:block;height:1px;border:0;border-top:1px solid #cccccc;margin:1em 0;padding:0}input,select{vertical-align:middle}

html{width:100%;height:100%;margin:0;background:#AA076B;background:linear-gradient(to top, #61045F, #AA076B)}.container{display:flex;flex-direction:column;align-items:center;justify-content:center;color:white;text-align:center}.logo{max-width:250px;height:auto;margin:3vmin 0 0 0}.header-section{margin:0}.header-section h2{font-family:'Warnes', cursive;font-size:calc(24px + (36 - 24) * ((100vw - 300px) / (1800 - 300)));line-height:calc(28px + (48 - 28) * ((100vw - 300px) / (1800 - 300)));letter-spacing:2px;color:#fff;text-decoration:none;-webkit-animation:glow 1s ease-in-out infinite alternate;animation:glow 1s ease-in-out infinite alternate;transition:all 1.0s}.header-section h2:hover{color:#0DB1DB;-webkit-animation:neon3 1.0s ease-in-out;animation:neon3 1.0s ease-in-out}@-webkit-keyframes glow{from{text-shadow:0 0 5px #fff, 0 0 10px #fff, 0 0 15px #0073e6, 0 0 20px #0073e6, 0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6}to{text-shadow:0 0 10px #fff, 0 0 20px #fff, 0 0 30px #0073e6, 0 0 40px #0073e6, 0 0 50px #0073e6, 0 0 60px #0073e6, 0 0 70px #0073e6}}@keyframes glow{from{text-shadow:0 0 5px #fff, 0 0 10px #fff, 0 0 15px #0073e6, 0 0 20px #0073e6, 0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6}to{text-shadow:0 0 10px #fff, 0 0 20px #fff, 0 0 30px #0073e6, 0 0 40px #0073e6, 0 0 50px #0073e6, 0 0 60px #0073e6, 0 0 70px #0073e6}}@-webkit-keyframes neon3{from{text-shadow:0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #FFDD1B, 0 0 70px #FFDD1B, 0 0 80px #FFDD1B, 0 0 100px #FFDD1B, 0 0 150px #FFDD1B}to{text-shadow:0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FFDD1B, 0 0 35px #FFDD1B, 0 0 40px #FFDD1B, 0 0 50px #FFDD1B, 0 0 75px #FFDD1B}}@keyframes neon3{from{text-shadow:0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #FFDD1B, 0 0 70px #FFDD1B, 0 0 80px #FFDD1B, 0 0 100px #FFDD1B, 0 0 150px #FFDD1B}to{text-shadow:0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FFDD1B, 0 0 35px #FFDD1B, 0 0 40px #FFDD1B, 0 0 50px #FFDD1B, 0 0 75px #FFDD1B}}h2.progress-header{margin:1vmin 0 2vmin 0;font-family:'Lora', serif;font-size:calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));font-weight:800;color:#ffc107}h1{margin:2vmin 0}.question-text{margin:0 4vmin 2vmin 4vmin;font-family:'Roboto', sans-serif;font-size:calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));line-height:calc(28px + (36 - 28) * ((100vw - 300px) / (1800 - 300)));font-weight:800}.answer{width:900px;max-width:90vw;margin-top:10px;padding:1vmin;font-family:'Lora', serif;font-size:calc(18px + (24 - 18) * ((100vw - 300px) / (1800 - 300)));line-height:calc(22px + (28 - 22) * ((100vw - 300px) / (1800 - 300)));text-align:left;background-color:#fff;border:1px solid #fff;border-radius:10px;cursor:pointer;transition:ease-in-out, width 0.35s ease-in-out}.answer:hover{color:#fff;background-color:#0DD1C4}.answer:focus{color:#fff;outline:none}.selected{font-weight:700;-webkit-text-fill-color:#fff;background-color:#18A2AB}.letter{width:30px;height:28px;top:0;padding:5px;left:0;font-family:'Lora', serif;text-align:center;color:#4f4c4c}.results{margin:3vmin 0 0 0}.results h2{margin:1vmin 0 2vmin 0;font-family:'Roboto', sans-serif;font-size:calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)));font-weight:800;color:#ffc107}.results ul{margin:0 4vmin;font-size:calc(18px + (26 - 18) * ((100vw - 300px) / (1800 - 300)));line-height:calc(24px + (32 - 24) * ((100vw - 300px) / (1800 - 300)))}.results span.correct{color:#0DB1DB;font-weight:800}.results span.failed{color:#f27c7c;font-weight:800}.error{color:#f27c7c;font-weight:800}.btn{margin-top:20px;padding:10px 50px;text-transform:uppercase;font-size:18px;cursor:pointer;color:#4f4c4c;background-color:#f4f4f4;transition:0.4s;border:2px solid #fff;border-radius:5px}.btn:hover{color:#fff;background-color:#ffce3c;border:2px solid #fff;-webkit-transform:scale(1.1);transform:scale(1.1)}.btn-primary{margin:2vmin 0;color:#4f4c4c;background-color:#ffc107;font-weight:bold}#no-wrap{white-space:nowrap}

